import { useContext } from "react";
import { SiteContext } from "../context";
import { Link } from "react-router-dom";

export default function BurgerMenu() {
    const { setSubBurgerItem, burgerMenuSwitcher } = useContext(SiteContext);

    function subBurgerMenuSwitcher(openedItem) {
        burgerMenuSwitcher();
        setSubBurgerItem(openedItem);
    }

    return (
        <div className="burger-menu">
            <div className="burger-menu__burger-close burger-close" onClick={burgerMenuSwitcher}></div>
            <div className="burger-menu__nav">
                <ul>
                    <li className="burger-menu__link"><a onClick={() => subBurgerMenuSwitcher('main')}></a></li>
                    <li className="burger-menu__link"><a onClick={() => subBurgerMenuSwitcher('aboutCompany')}> </a></li>
                    <li className="burger-menu__link"><a onClick={() => subBurgerMenuSwitcher('catalog')}></a></li>
                    <li className="burger-menu__link"><a onClick={() => subBurgerMenuSwitcher('cooperation')}></a></li>
                    <li className="burger-menu__link"><a onClick={() => subBurgerMenuSwitcher('services')}></a></li>
                    <li className="burger-menu__link"><Link to="/contacts" onClick={burgerMenuSwitcher}></Link></li>
                    <li className="burger-menu__link"><Link to="/news" onClick={burgerMenuSwitcher}></Link></li>
                </ul>
            </div>
        </div>
    )
}