import { Link } from "react-router-dom"
import { useContext } from "react"
import { SiteContext } from "../context"

export default function Footer() {
    const { scrollingToTop, popupSwitcher } = useContext(SiteContext);

    return (
        <section className="footer">
            <div className="footer__background"></div>
            <div className="footer__container _container">
                <div className="footer__top">
                    <div className="footer__logo-section">
                        <div className="footer__logo"></div>
                        <div className="footer__title-section">
                            <h2 className="footer__title">  </h2>
                            <p className="footer__subtitle"> </p>
                        </div>
                    </div>
                    <div className="footer__nav">
                        <ul>
                            <li><Link to="/" onClick={scrollingToTop}></Link></li>
                            <li><Link to="/about-company" onClick={scrollingToTop}> </Link></li>
                            <li><Link to="/catalog" onClick={scrollingToTop}></Link></li>
                            <li><Link to="/cooperation" onClick={scrollingToTop}></Link></li>
                            <li><Link to="/services" onClick={scrollingToTop}></Link></li>
                            <li><Link to="/contacts" onClick={scrollingToTop}></Link></li>
                            <li><Link to="/news" onClick={scrollingToTop}></Link></li>
                            <li><Link to="/vacancies" onClick={scrollingToTop}></Link></li>
                        </ul>
                        <ul>
                            <li><Link to="/catalog/catalog-item" onClick={scrollingToTop}> </Link></li>
                            <li><Link to="/catalog/catalog-item" onClick={scrollingToTop}> </Link></li>
                            <li><Link to="/catalog/catalog-item" onClick={scrollingToTop}>.    </Link></li>
                            <li><Link to="/catalog/catalog-item" onClick={scrollingToTop}> </Link></li>
                            <li><Link to="/catalog/catalog-item" onClick={scrollingToTop}> </Link></li>
                            <li><Link to="/catalog/catalog-item" onClick={scrollingToTop}> </Link></li>
                            <li><Link to="/catalog/catalog-item" onClick={scrollingToTop}> </Link></li>
                        </ul>
                    </div>
                    <div className="footer__contacts">
                        <p className="footer__email">Email: Emaratcompany@gmail.com</p>
                        <p className="footer__phone">.: 8 927 000 00 00 </p>
                        <div className="footer__phone-button phone-button" onClick={() => popupSwitcher('form-open')}> </div>
                        <p className="footer__address"></p>
                    </div>
                </div>
                <div className="footer__bottom">
                    <p> </p>
                    <p> </p>
                    <p>2023   </p>
                </div>
            </div>
        </section>
    )
}